



















import Vue from 'vue'
import { Defaults, jsPlumb } from 'jsplumb'
import { mapGetters } from 'vuex'

const jsplumbConf: (el: Element) => Defaults = (el) =>  ({
	Anchor: ['Continuous', { orientation: 1 }],
	DragOptions: { cursor: 'grab', zIndex: 100, containment: 'true' },
	Connector: ['Flowchart', { stub: [40, 60], gap: 10, cornerRadius: 5, alwaysRespectStubs: true }],
	ConnectionOverlays: [
		['Arrow', { direction: 1, location: 1, width: 15, paintStyle: { fill: '#ccc' } }],
	],
	Container: el,
	Endpoint: 'Blank',
	PaintStyle: { stroke: '#ccccccaa', strokeWidth: 2 },
})

export default Vue.extend({
	props: {
		name: {
			type: String,
			required: true,
		},
	},
	data() {
		return {
			ready: false,
			rerender: 0,
		}
	},
	computed: {
		...mapGetters({
			readonly: 'querying',
			tables: 'tables',
		}),
	},
	watch: {
		ready() {
			if (this.ready === true) this.initialize()
		},
		tables() {
			this.ready = false
			this.rerender++
		},
	},
	methods: {
		initialize() {
			const instance = jsPlumb.getInstance(jsplumbConf(this.$el))

			this.tablePosition()

			instance.ready(() => {
				instance.bind('connection', function (connInfo) {
					// @ts-expect-error
					const label = connInfo.connection.getLabelOverlay()
					label.addClass('canvas-label')
				})

				document.querySelectorAll('.sq-table').forEach(
					el => instance?.draggable(
						el,
						{
							containment: 'true',
							stop: ({ el, pos }) => this.sessionPosition(el, pos),
						}
					)
				)

				Object.values(this.tables as Indexable<Table>).forEach(
					table => {
						const foreigns = table
							.fields
							.filter(field => field.foreign)
							.map(
								field => ({
									table: Object.keys(field.foreign || {})[0],
									tableField: Object.values(field.foreign || {})[0],
									field: field.name,
								})
							)
						
						foreigns.forEach(
							relation => {
								instance.connect({
									source: table.name,
									target: relation.table,
									label: `${relation.field}:${relation.tableField}`,
								})
							}
						)
					}
				)
			})
		},
		tablePosition() {
			const { offsetWidth } = this.$el as HTMLElement
			const perLine = Math.floor((offsetWidth - 40) / 320)
			let maxHeight = 0

			this.$el.querySelectorAll('.canvas-table').forEach(
				(table, i) => {
					const { style, offsetHeight } = table as HTMLElement
					const numberPx = (num: number) => `${num}px`
					const position = { left: 0, top: 0 }

					const previous = sessionStorage
						.getItem(table.id)?.split(',')
						.map(pos => Number(pos))
					
					if (previous) {
						const [ x, y ] = previous
						const { clientWidth } = this.$el
						position.left = (x + 280) > clientWidth ? clientWidth - 280 : x
						position.top = y
					} else {
						if (offsetHeight > maxHeight) maxHeight = offsetHeight + 350
						position.left = (i % perLine) * 320 + 40
						position.top = Math.floor(i / perLine) * maxHeight + 100
					}

					style.left = numberPx(position.left)
					style.top = numberPx(position.top)

					this.sessionPosition(table, [ position.left, position.top ])
				}
			)
		},
		sessionPosition(table: Element, [ x, y ]: [number, number]) {
			sessionStorage.setItem(table.id, `${x},${y}`)
		},
	},
})
