import Vue from 'vue'
import Vuex from 'vuex'

import dataStore from '@/store/data'
import schemaStore from '@/store/schema'
import queryStore from '@/store/query'
import { Interpreter } from '@/sql'

Vue.use(Vuex)

export interface RootState {
	appLoading: boolean;
	appMobile: boolean;
}

export default new Vuex.Store<RootState>({
	modules: {
		dataStore,
		queryStore,
		schemaStore,
	},
	state: {
		appLoading: false,
		appMobile: window.innerWidth < 500,
	},
	mutations: {
		startLoading(state: RootState) {
			state.appLoading = true
		},
		stopLoading(state: RootState) {
			state.appLoading = false
		},
	},
	actions: {
		async loadFile({ dispatch }, file: File) {
			const name = file.name.replace('.sql', '')
			const content = await file.text()

			return new Promise((resolve, reject) => {
				dispatch('loadSchema', { name, content })
					.then(() => resolve(null))
					.catch(error => reject(error))
			})
		},
		async loadLocal({ dispatch }, name: string) {
			const content = (await require(`@/store/databases/${name}.sql`)).default
			dispatch('loadSchema', { name, content })
		},
		async loadSchema({ commit, dispatch }, { name, content }: Indexable<string>) {
			try {
				const { tables, data } = Interpreter.import(content)
				dispatch('newSchema', name)
				
				Object.entries(tables).forEach(
					([name, table]) => {
						commit('updateTable', { name, table })
						commit('updateData', { name, rows: data[name] })
					}
				)
			} catch (error) {
				throw error
			}
		},
	},
	getters: {
		appLoading: state => state.appLoading,
		appMobile: state => state.appMobile,
		schemas: () => ['cartoon', 'company' ],
	},
})
