interface TypeInfo {
	size?: boolean;
	precision?: boolean;
	validate?: TypeRegex;
}

enum TypeRegex {
	BOOL = '^(TRUE|FALSE)$',
	CHAR = '^.+$',
	DATE = '([0-9]{4})\\-(0[0-9]|1[0-2])\\-([0-2][0-9]|3[0-1])',
	INT = '^\\d+$',
	REAL = '^\\d+(\\.\\d+)?(E\\-\\d{1,3})$',
	TIME = '^([0-1][0-9]|2[0-3]):([0-5][0-9]):([0-5][0-9])(\\.\\d{7})?$',
}

export class Type implements Indexable<TypeInfo> {
	[key: string]: TypeInfo,

	static BIGINT = {
		validate: TypeRegex.INT,
	}
	static BOOLEAN = {
		validate: TypeRegex.BOOL,
	}
	static CHAR = {
		size: true,
		validate: TypeRegex.CHAR,
	}
	static CHARACTER = {
		size: true,
		validate: TypeRegex.CHAR,
	}
	static DATE = {
		validate: TypeRegex.DATE,
	}
	static DECIMAL = {
		precision: true,
		validate: TypeRegex.REAL,
	}
	static DOUBLE = {
		precision: true,
		validate: TypeRegex.REAL,
	}
	static INT = {
		size: true,
		validate: TypeRegex.INT,
	}
	static INTEGER = {
		size: true,
		validate: TypeRegex.INT,
	}
	static NCHAR = {
		size: true,
		validate: TypeRegex.CHAR,
	}
	static NUMERIC = {
		size: true,
		validate: TypeRegex.REAL,
	}
	static NVARCHAR = {
		size: true,
		validate: TypeRegex.CHAR,
	}
	static REAL = {
		size: true,
		validate: TypeRegex.INT,
	}
	static SMALLINT = {
		size: true,
		validate: TypeRegex.INT,
	}
	static TEXT = {
		size: true,
		validate: TypeRegex.CHAR,
	}
	static TIME = {
		size: true,
		validate: TypeRegex.TIME,
	}
	static TINYINT = {
		size: true,
		validate: TypeRegex.INT,
	}
	static VARCHAR = {
		size: true,
		validate: TypeRegex.CHAR,
	}
}

export const Types = Object.keys(Type)
