



























































































import Vue from 'vue'
import { mapGetters } from 'vuex'
import { Type } from '@/type'

export default Vue.extend({
	props: {
		field: {
			type: Object as () => Field,
			required: true,
		},
		foreignOptions: {
			type: Object as () => Indexable<string[]>,
			required: true,
		},
		types: {
			type: Array as () => Array<string>,
			required: true,
		},
	},
	data() {
		const editable: Field = { ...this.field }

		return {
			foreign: {
				table: editable.foreign ? Object.keys(editable.foreign)[0] : '',
				field: editable.foreign ? Object.values(editable.foreign)[0] : '',
			},
			editable,
			editing: !editable.name.length && !editable.type.length,
			Type,
		}
	},
	computed: {
		...mapGetters({
			readonly: 'querying',
		}),
		primary(): boolean { return this.editable.primary === true },
		notnull(): boolean { return this.editable.notnull === true },
		null(): boolean { return this.editable.null === true },
	},
	methods: {
		oneditcancel() {
			this.editable = { ...this.field }
			this.editing = false
			this.$emit('cancel')
		},
		oneditconfirm() {
			this.editing = false
			this.$emit('change', { ...this.editable })
		},
	},
	watch: {
		foreign: {
			deep: true,
			handler() {
				if (this.foreign.table.length && this.foreign.field.length) {
					this.editable.foreign = {
						[this.foreign.table]: this.foreign.field
					}
				} else {
					this.editable.foreign = {
						table: '',
						field: '',
					}
				}
			},
		},
		primary() {
			if (!this.primary) return
			this.editable.null = false
		},
		notnull() {
			if (!this.notnull) return
			this.editable.null = false
		},
		null() {
			if (!this.null) return
			this.editable.primary = false
			this.editable.notnull = false
		},
	},
})
