













import Vue from 'vue'

export default Vue.extend({
	data() {
		return {
			open: false,
		}
	},
	computed: {
		locale(): string {
			return this.$i18n.locale.toUpperCase()
		},
		locales(): string[] {
			return this.$i18n.availableLocales.map(local => local.toUpperCase())
		},
	},
	methods: {
		choose(local: string) {
			if (local !== this.locale) {
				this.$i18n.locale = local.toLowerCase()
			}
			this.open = false
		},
	},
})
